<template>
    <div>
        <!--  -->
        <a href="#" @click.prevent="go_back">
            <i class="el-icon-d-arrow-left"></i><br><br>
        </a>
        <el-card class="box-card"  shadow='hover' :body-style=body_style>
            <div slot="header" class="clearfix">
                <div style="font-size:14px;margin-bottom:0px;float:left" v-if="this.$route.query.projectName">
                    &nbsp;&nbsp;&nbsp;&nbsp;
                    {{this.$route.query.projectName}}
                    &nbsp;&nbsp;&nbsp;&nbsp;
                </div>
                <div style="font-size:14px;margin-bottom:0px;float:left" v-if="this.$route.query.type == 'app'">
                    app{{this.$route.query.appName}}
                </div>
                <div style="font-size:14px;margin-bottom:0px;float:left" v-if="this.$route.query.type == 'api'">
                    {{this.$route.query.appName}}
                </div>
                <div style="font-size:14px;float:left" v-if="this.$route.query.type == 'web'">
                    {{this.$route.query.appName}}
                </div>
                <div style="float: right;margin-bottom:10px;text-align:center">
                    <!--  -->
                    <el-button
                        type="primary"
                        size="mini"
                        @click="edit_appsrc" style=""
                        v-if='this.$route.query.userid==userId || userId==2'>{{editBtnText}}
                    </el-button>
                    <!--  -->
                    <el-button
                        type="primary"
                        size="mini"
                        @click="get_appTest"
                        :loading="loading">{{testBtn}}
                    </el-button>
                </div>
            </div>
            <div>
                <editor
                    height="800"
                    v-model="code"
                    :options="options"
                    lang="python"
                    theme="crimson_editor">
                </editor>
            </div>
        </el-card>
    </div>
</template>

<script>
/* eslint-disable */
import editor from 'vue2-ace-editor'
import 'brace/mode/python'
import 'brace/mode/javascript'
import 'vue2-ace-editor/node_modules/brace/snippets/python'
import 'brace/theme/crimson_editor'
import 'brace/theme/chrome'
import 'brace/ext/language_tools'
export default {
    name:'Src',
    data() {
        return {
            axios: this.axios,
            url: this.url,
            userId: this.storage.getItem('userId'),
            token: this.storage.getItem('token'),
            code:'',
            old_code:'',
            testBtn:'',
            loading: false,
            body_style: {
                padding: '0px',
                background:'#eee'        
                },
            options: {
                // 
                enableBasicAutocompletion: true,
                // if
                enableSnippets: true,
                // 
                enableLiveAutocompletion: true,
                // 
                highlightActiveLine: true,
                // 
                readOnly: true
            }
        }
    },
    methods: {
        // 
        go_back() {
            if (this.editBtnText == '*') {
                this.$confirm('?', '', {
                    distinguishCancelAndClose: true,
                    type: 'warning',
                    confirmButtonText: '',
                    cancelButtonText: '',
                    }).then(() => {
                        this.$router.back(-1)
                    }).catch(() => {
                })
            }
            else {
                this.$router.back(-1)
            }
            
        },
        // 
        get_appTest() {
            this.$confirm('1 PC </br> \
                2 appium </br> \
                3  </br> \
                4 ', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
                dangerouslyUseHTMLString: true
                }).then(() => {
                    this.$message({
                        message: '',
                        type: 'success',
                        center: true
                    });
                    this.loading=true
                    this.testBtn='...'
                    var url = 'api/v1/saveSrc/'
                    var params_data = {
                        'userId':this.userId,
                        'token':this.token,
                        'type':this.$route.query.type,
                        'id':this.$route.query.id,
                    }
                    this.axios({
                        baseURL:this.url,
                        url:url,
                        method:'patch',
                        params:params_data,
                    }).then(response=>{
                        // 
                        if (!response.data.errcode) {
                            this.$message({
                                message: 'PASS',
                                type: 'success',
                                center: true,
                                showClose: true,
                                duration:0,
                            });
                        }
                        else {
                            this.$message({
                                message: response.data.errmsg,
                                type: 'error',
                                center: true,
                                showClose: true,
                                duration:0,
                            })
                        }
                        this.loading=false
                        this.testBtn=''
                    },error=>{
                        this.$message({
                            message: '',
                            type: 'error',
                            center: true,
                            showClose: true,
                            duration:0,
                        })
                        this.loading=false
                        this.testBtn=''
                    })
                }).catch(() => {        
            });
        },
        // 
        get_appSrc() {
            var url = 'api/v1/saveSrc/'
            var params_data = {
                'userId':this.userId,
                'token':this.token,
                'type':this.$route.query.type,
                'id':this.$route.query.id,
            }
            this.axios({
                baseURL:this.url,
                url:url,
                method:'get',
                params:params_data,
            }).then(response=>{
                this.code=response.data.src
                this.old_code = response.data.src
            },error=>{
                this.$message({
                        message: '',
                        type: 'error',
                        center: true,
                        showClose: true,
                    })
                this.$router.push('/')
            })
        },
         // 
        edit_appsrc() {
            var params_data = {'userId':this.userId,'token':this.token}
            var data = {
                type:this.$route.query.type,
                id:this.$route.query.id,
                src:this.code
            }
            this.axios({
                baseURL:this.url,
                url:'api/v1/saveSrc/',
                method:'post',
                params:params_data,
                data:data,
            }).then(response=>{
                // 
                if (!response.data.errcode) {
                    this.$message({
                        message: '',
                        type: 'success',
                        center: true
                    });
                    this.old_code = this.code
                }
                else {
                    this.$message({
                        message: "",
                        type: 'error',
                        center: true
                    })
                }
            },error=>{
                this.$message({
                    message: '',
                    type: 'error',
                    center: true
                })
            })
        }
    },
    components: {
        editor
    },
    computed: {
        editBtnText: function() {
            if (this.code != this.old_code) {
                return '*'
            }
            else {
                return ' '
            }
        }
        
    },
    created() {
        this.get_appSrc()
        if (this.$route.query.userid==this.userId || this.userId==2) {
            this.options.readOnly = false
        }
    }
}
</script>


